import React, { useState, useEffect } from "react";
import {
  makeStyles,
  Grid,
  Card,
  Container,
  CardActionArea,
  CardMedia,
} from "@material-ui/core";
import { useHistory, useParams} from "react-router-dom";
import Carousel from "react-elastic-carousel";
import axios from "axios";

const userStyles = makeStyles(() => ({
  root: {
    minWidth: 140,
    flexGrow: 1,
  },
}));

export default function MovieRecommendations() {
  const classes = userStyles();
  let { id } = useParams();
  const [popular, setPopular] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://api.themoviedb.org/3/movie/${id}/recommendations?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&page=1`
      )
      .then((response) => {
        if (response.data != null) {
          setPopular(response.data.results);
        } else {
          setPopular(null);
        }
      });
  }, []);

  const history = useHistory();

  return (
    <div>
      <Container
        maxWidth="xl"
        style={{ backgroundColor: "black", height: "45vh", paddingTop: 9 }}
      >
        <h2
          className="popular"
          style={{ paddingBottom: "1rem", color: "white" }}
        >
          Recommendations
        </h2>
        <Grid
          container
          spacing={3}
          direction="row"
          justify="flex-start"
          alignItems="flex-start"
        >
          <Carousel
            itemsToShow={6}
            itemsToScroll={1}
            pagination={false}
            showArrows={true}
          >
            {popular.map((popular, index) =>
              popular.poster_path !== null ? (
                <Grid item xs={12} sm={6} key={index}>
                  <Card
                    className={classes.root}
                    onClick={() => {
                      history.push(`/movie/${popular.title}/${popular.id}`)
                      window.location.reload(true)
                    }}
                  >
                    <CardActionArea>
                      <CardMedia
                        component="img"
                        objectFit="cover"
                        image={
                          "https://image.tmdb.org/t/p/w500" +
                          popular.poster_path
                        }
                        title={popular.title}
                        id={popular.id}
                      />
                    </CardActionArea>
                  </Card>
                </Grid>
              ) : null
            )}
          </Carousel>
        </Grid>
      </Container>
    </div>
  );
}
